body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* FLEX */
.flex { display: flex; }

.horizontal, .horizontal-only { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-center, .flex-start, .flex-end { justify-content: center; }
.flex-center { align-items: center; }
.flex-start { align-items: flex-start; }
.flex-end { align-items: flex-end; }

.flex-center-end {
  justify-content: flex-end;
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }

/* COMPONENTS */
.container, .container-big, .container-small {
  width: calc(100% - 40px);
  padding: 10px 20px;
  max-width: 1200px;
  margin: 0 auto;
}
.container-small { max-width: 900px; }
.container-big { max-width: 1400px; }

.logo {
  max-width: 200px;
  cursor: pointer;
}

.card {
  border: 0;
  border-radius: 10px;
  box-shadow: 0 0 5px #7777;
  padding: 30px;
  margin: 20px;
  width: calc(100% - 100px);
}
.card-message {
  height: 100px;
}
.right {
  float: right;
}

.text-center {
  text-align: center;
}

td {
  padding: 8px 12px;
}
tr > td:first-child {
  background-color: #202020;
  color: #FEFEFE;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 250px;
}

.Header {
  background-color: #AB092F;
}
input.pesquisa {
  border: 0;
  border-radius: 10px;
  width: 85%;
  padding: 5px 15px;
  font-size: 1rem;
}
.btn {
  cursor: pointer; }
.btn.btn-pesquisa {
  border: 0;
  background-color: transparent;
  margin-left: -40px;
}
.btn.btn-cart {
  background-color: transparent;
  color: #FFF;
  border: 0;
  font-weight: bold;
}
.itens-header {
  color: #FFF;
  font-weight: bold;
}
.cursor {
  cursor: pointer;
}

.fa-star {
  color: #ecec20;
}

.Footer {
  background-color: #171717;
  color: #FAFAFA;
  padding: 10px 0;
}

/* CATEGORIAS */
.Categorias {
  background-color: #171717;
  color: #FFF;
  font-weight: bold;
}
.categoria-item {
  padding: 10px;
  text-align: center;
  cursor: pointer;
  font-size: 0.9rem;
}
.categoria-item:hover {
  background-color: #3c3c3c;
}

/* HEROSHOP */
.breadcrumbs {
  padding: 25px 0;
  color: #999;
  font-size: 0.9rem; }

.mini-pic {
  border: 1px solid #aaaa;
  cursor: pointer; }
.main-pic-container, .mini-pics {
  max-width: 800px;
  margin-right: 20px;
  justify-content: flex-end; }
.mini-pic-container {
  margin: 5px 5px 5px 0;
}
.pics {
  display: flex; }

.titulo h2 {
  margin: 0;
  margin-bottom: 0px; }
.titulo span {
  color: #787878;
  font-size: 0.8rem; }

.rating {
  margin: 8px 0;
}
.rating .avalie {
  padding: 0 5px;
  font-size: 0.9rem;
  color: #444;
  cursor: pointer;
}

.price h2 {
  font-size: 2.2rem;
  margin-bottom: 3px;
}
.price .opcoes-pagamento {
  padding: 0;
  color: #777;
  font-size: 0.9rem;
}
.price .opcoes-pagamento select {
  font-size: 0.8rem;
}

.buy-button button {
  background-color: #077d0b;
  color: #ffffff;
  font-size: 1.8rem;
  border: 0;
  margin: 30px 0;
  padding: 15px 40px;
  font-weight: bold;
  cursor: pointer;
}
.buy-button i {
  padding: 0 10px;
  font-size: 1.5rem;
}
.buy-button button:hover {
  background-color: #085a0b;
}

.aviso-lock {
  color: #777;
  font-size: 0.95rem;
}

.card-price {
  background-color: #fcfcfc;
  padding: 60px 40px;
  border: 1px solid #dddd;
}

.CategoriasFooter {
  background-color: #AB092F;
  height: 60px;
}

.Institucionais {
  background-color: #202020;
  color: #FFF;
  padding: 30px 0;
}
.redes-sociais {
  padding: 5px;
  font-size: 1.5rem;
  cursor: pointer;
}
.redes-sociais:first-child{
  padding-left: 0;
}

.cartoes, .selos {
  background-color: #F7F7F7;
  width: 300px;
  padding: 10px 5px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}
.selos {
  width: 300px; }
.cartoes {
  width: 420px;
  background-color: #FFFFFF; }

.sac {
  font-weight: bold;
}

@media screen and (max-width: 720px){
  .padding-mb-10 {
    padding: 10px 0;
  }
  .overflow-x {
    overflow-x: scroll;
  }
  .categoria-item {
    padding: 10px 5px;
    width: 60px;
  }
  .categoria-item span {
    width: 140px;
  }
  input.pesquisa {
    width: 95%;
  }
  .main-pic-container, .mini-pics {
    margin-right: 0;
  }
  .buy-button button {
    width: 100%;
  }
  .container-small {
    margin: 0;
    padding: 0;
  }
  .cartoes, .cartoes img {
    width: 100%;
  }
  .no-mb-margin {
    margin: 0;
    width: 82%;
  }
}